import React, { Component } from 'react';

class InputTodo extends Component {

    constructor(props) {
        super(props);
        
        this.initialState = {
            title: ''
        };

        this.state = this.initialState;
    }

    handleChange = event => {
        const {name, value} = event.target;
    
        this.setState({
            title: value
        });
    }
        render() {
        
            return (
                <form>
                <input type="text" className="form-control add-todo" placeholder="Add todo" name = 'todoText' onChange={this.handleChange} value={this.state.todoText} />
                    <button className="btn btn-success" onClick={ () => this.props.addTotodoList( this.state ) }>add To-Do
                    </button>
                   </form>
            )
        }
    
   
}
export default InputTodo;


