import React from 'react';
import TodonotdoneList from './ToDoNotDoneList';
import InputTodo from './InputTodo'

export default class MainContainer extends React.Component{
    state = {
        data: []
    };
   
    handleSubmit = todoData => {
        this.setState({
            data: [...this.state.data, todoData]
        })
    }
    render(){
        const { data } = this.state;
        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="todolist not-done">
                        <h1>Todos</h1>
                        <InputTodo addTotodoList={this.handleSubmit}/>
                                <hr />
                                <TodonotdoneList todoListData = {data} />
                            <div className="todo-footer">
                                <strong><span className="count-todos"></span></strong> Items Left
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="todolist">
                        <h1>Already Done</h1>
                            <ul id="done-items" className="list-unstyled">
                                <li>Some item <button className="remove-item btn btn-default btn-xs pull-right"><span className="glyphicon glyphicon-remove"></span></button></li>
                                
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}